import styled from 'styled-components'

export const Card = styled.div`

    margin: 10px;
    display: grid;
    grid-template-rows: 200px 100px;
    width: 200px;
    height: 300px;

    img {
        border-radius: 6px;
    }

`

export const Etiqueta = styled.div`

    padding: 12px 0;
 
`



